import React, { useState } from "react";

const dynamicForm = {
  email: "",
  password: "",
  address: ""
};

const Form = () => {
  const [form, setForm] = useState([dynamicForm]);

  const addFormHandler = () => {
    setForm([...form, { ...dynamicForm }]);
  };

  const deleteFormHandler = () => {
    const copy = [...form];
    if (copy.length === 1) {
      return null;
    } else {
      copy.pop();
      setForm(copy);
    }
  };

  const onInputChangeHandler = (e) => {
    const name = e.target.name;
    const selectedForm = e.target.dataset.idx;
    const currentValue = e.target.value;

    // [e.target.dataset.id][e.target.name].value = e.target.value;

    console.log(selectedForm);

    const copyOfForm = [...form];

    copyOfForm[selectedForm][name] = currentValue;

    setForm(copyOfForm);
  };

  const onSubmitHandler = (e) => {
    e.preventDefault();

    console.log(form);
  };

  console.log(form, "form");

  return (
    <form className="mb-4">
      {form.map((item, i) => {
        return (
          <div className="form" key={i}>
            <div className="form-group">
              <label className="d-block ">Email {i}</label>
              <input
                type="text"
                placeholder="Email"
                name="email"
                data-idx={i}
                value={item.email.value}
                onChange={onInputChangeHandler}
              />
            </div>

            <div className="form-group">
              <label className="d-block ">Password {i}</label>
              <input
                type="password"
                placeholder="password"
                name="password"
                data-idx={i}
                onChange={onInputChangeHandler}
                value={item.password.value}
              />
            </div>

            <div className="form-group">
              <label className="d-block ">Address {i}</label>
              <input
                type="text"
                placeholder="Address"
                name="address"
                data-idx={i}
                onChange={onInputChangeHandler}
                value={item.address.value}
              />
            </div>
            <hr />
          </div>
        );
      })}

      <div className="d-flex justify-content-between">
        <button
          type="button"
          className="btn btn-primary"
          onClick={addFormHandler}
        >
          Add More
        </button>
        <button
          type="button"
          className="btn btn-danger"
          onClick={deleteFormHandler}
        >
          Remove
        </button>
      </div>
      <button
        type="button"
        className="btn btn-warning px-4 mt-4"
        onClick={onSubmitHandler}
      >
        Submit
      </button>
    </form>
  );
};

export default Form;
